<template>
    <div class="Move">
        <div class="img_list">
            <div class="img_box clearfloat">
                <div v-for="(car,index) in list" :key="index" @click="viewImg(index+6)" class="img_box_div">
                  <img  :src="car.img">
                   <div class="text-bottom three_line">
                       <span> {{car.name}}</span>
                    </div>
                </div>
            </div>
        </div>
  </div>
</template>
<script>
export default {
  name: 'Move',
  data () {
    return {
       list:[]
    }
  },
  methods:{
      pushList(){
          var name = [
              '傅抱石.许麟庐.郁风.郭沫若','许麟庐.傅抱石',
              '华君武、黄胄、李苦禅、吴作人、陈复礼、李可染、萧淑芳、白雪石、许麟庐，黄永玉',
              '胡絜青、李万春、李苦禅，许麟庐等','陆俨少、秦岭云、魏金非，谢稚柳，许麟庐',
              '秦岭云，周怀民、陆俨少，许麟庐、陈大羽、','陆俨少、华君武、许麟庐、亚明、丁井文',
              '陆俨少、关良，许麟庐','李苦禅，许麟庐',
              '许麟庐，李苦禅','张伯驹、潘素夫妇在许麟庐芝麻胡同家中',
              '张伯驹 许麟庐','1994年，刘海粟参加在香港举办的许麟庐画展并题字。左起：许麟庐、刘海粟、王龄文、许化迟',
              '关良 ，许麟庐，王龄文','陆俨少、许麟庐',
              '溥杰、许麟庐','王龄文、胡絜青 许麟庐',
              '朱屺瞻。许麟庐','许麟庐，唐云',
              '启功、秦岭云、许麟庐、王龄文、刘继瑛、林锴','郁风、黄苗子、许麟庐、吴祖光、齐良迟，丁聪在竹箫斋','关山月，许麟庐',
              '黎雄才 许麟庐','许麟庐,魏紫熙','许麟庐，宋文治','许麟庐、亚明',
              '启功、许麟庐、王世襄，黄苗子','启功、许麟庐','胡爽庵、许麟庐','王世襄、许麟庐',
              '许麟庐、谢添','张君秋、李万春、许麟庐','张君秋，许麟庐',
              '许麟庐、齐良迟、梅葆玖、陈铎夫妇','许麟庐 丁聪','黄苗子、许麟庐','许麟庐，黄苗子、黄永玉、黄永厚',
              '黄苗子、王世襄、丁聪、黄永玉、许化迟、孙燕华等','黄苗子、王世襄、许麟庐、许化迟','黄苗子、许麟庐、许化迟',
              '许麟庐、冯其庸','黄永玉，许麟庐','许麟庐、蓝天野','许麟庐、韩静霆','许麟庐、宋涤','许麟庐、王鸿勋','年中国美术馆许麟庐画展开幕式',
              '2010年北京画院美术馆《写意人生》许麟庐画展开幕式（左二起：吴洪亮，王明明、解永全，徐明，邹佩珠，马凯，许麟庐，王龄文，陈进玉，冯其庸，郑闻慧，雷振芳',
              '许麟庐与弟子门在和平艺苑（左起：王作勤、陈志明、韩静霆、许麟庐、宋涤、王龄文、雷振芳、王鸿勋、许化迟）',

              '许化迟与饶宗颐先生','吴祖光 许化迟 宋文治','许化迟与黄永玉先生在竹箫斋',
              '2003年吴冠个人画展在和平艺苑隆重举行','许化迟与吴冠中先生 ','欧阳中石、许麟庐',
              '许化迟与齐良迟先生','许化迟与黄永玉先生','黄苗子、黄永玉、许化迟。',
              '许化迟夫妇与廖静文先生','许化迟夫妇冯其庸先生','许麟庐 黄苗子 黄永玉 许化迟',
              '许麟庐 黄苗子 许化迟','李默然、李龙吟、许化迟、陈志明','许化迟与金庸先生',
              '舒乙夫妇、许化迟夫妇','许麟庐艺术研讨会','基辛格博士在和平艺苑','李可染艺术研讨会',
              '普京前夫人柳德米拉与许化迟夫妇合影','奥地利总统海因茨 菲舍尔与许化迟夫妇在和平艺苑合影',
              '许化迟夫妇与奥地利总统夫人玛吉特 菲舍尔合影'
              ]
          for(var i = 0;i<name.length;i++){
              this.list.push(
                  {
                      img:require('../assets/move/'+(i+7)+'-m.jpg'),
                      name:name[i]
                  }
              )
          }
      },
       viewImg(type){
          this.$router.push({path:'/VimeImage',query:{id:type}});
      }
  },
  mounted(){
     this.pushList();
  }
}
</script>

<style lang="less" scoped>
  .img_list{
      width: 1170px;
      margin: 0 auto;
  }
  .img_box{
     div{
          width: 380px;
          float: left;
          margin-bottom: 10px;
          margin-right: 15px;
          cursor: pointer;
          img{
              width: 100%;
               height: 300px;
               display: block;
               background: #000;
          }
      }
      div:nth-child(3n){
          margin-right: 0;
      }
      .text-bottom{
          height:75px;
         background: #EFCAA9;
         text-align: center;
         display: table-cell;
         vertical-align: middle;
         font-size: 18px;
        color: #79242F;
        padding: 10px;
        span{
            display: inline-block;
            vertical-align: middle;
        }
      }
  }
  
</style>


